@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
* {
  box-sizing: border-box;
}



body {
  font-family: "Montserrat", sans-serif;
}

.App {
  min-height: 100vh;
  background: white;
}

.header {
  text-align: center;
  padding: 20px 40px 60px 40px;
}

.title {
  color: #f9748f;
  font-weight: 200;
  font-size: 3rem;
}

.subtitle {
  color: #f9748f;
  font-weight: 200;
  font-size: 1.5rem;
}

.search-form {
  min-height: 5vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-left: 40px;
}

.recipes {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.search-filter {
  max-width: 700px;
  margin: 0 auto;
}

.recipe {
  border-radius: 60px;
  padding: 20px 0;
  margin: 20px;
  width: 400px;
  box-shadow: 8px 5px 5px 2px #9e9c9c26;
  letter-spacing: 1px;
  border: 1px solid #80808024;
}


.recipe_title {
  text-align: center;
  font-size: 1.6rem;
  color: #565656;
}

.recipe h2 {
  margin: 0;
  padding: 10px 20px 0px 20px;
  color: #565656;
  font-size: 1.3rem;
}

.image_div {
  width: 100%;
  overflow: hidden;
  margin: 0 auto;
}

.image {
  display: block;
  height:500px;
  width: 100%;
  margin: -33% 0;
}

.calories {
  text-align: center;
}

.ingredients {
  list-style-type: none;
  padding: 10px 20px;
}

.ingredient {
  padding: 5px 0;
  line-height: 1.4rem;
}

.ingredient i {
  color: #fb466a;
}

.search-bar {
  max-width: 420px;
  min-width: 320px;
  height: 50px;
  width: 45%;
  font-family: Arial, Helvetica, sans-serif;
  font-size: large;
  border: none;
  padding: 12px;
  box-shadow: 0 0 5px 0 #00000040;
  border-radius: 20px;
}

.search-bar::-webkit-input-placeholder {
  color: #00000070;
}

.search-bar:-ms-input-placeholder {
  color: #00000070;
}

.search-bar::-ms-input-placeholder {
  color: #00000070;
}

.search-bar::placeholder {
  color: #00000070;
}

.search-button {
  background-image: linear-gradient(to right, #f78ca0 0%, #f9748f 19%, #fd868c 60%, #fe9a8b 100%);
  border: 1px solid #fe9a8b;
  padding: 18px 20px;
  color: white;
  box-shadow: 0 0 5px 0 #00000040;
  border-radius: 20px;
  position: relative;
  right: 43px;
  top: -1px;
}

.search-bar:focus,
.search-bar:hover {
  box-shadow: 0 0 5px 2px #00000040;
  outline: none;
}

.search-button:focus {
  outline: none;
}

